import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, Panel, PanelHeader, Epic, Tabbar, TabbarItem, PanelHeaderButton, PanelHeaderContext, List, Cell, PanelHeaderContent} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import MapComponent from './components/map';
import Add from './components/add';
import '@vkontakte/vkui/dist/vkui.css';
import Icon28ChevronDownOutline from '@vkontakte/icons/dist/28/chevron_down_outline';
import Icon24Done from '@vkontakte/icons/dist/24/done';


const App = () => {
	const [filter, setFilter] = useState({zero: true,one: true,two: true,three: true,four: true});
	const [support, openSupport] = useState(false);
	const [coords, setCoords] = useState({lat: 0, long: 0});
	bridge.send("VKWebAppGetGeodata", {})
		.then(res=>{
			setCoords({lat: res.lat, long: res.long, zoom: 13});
		})
		.catch(e=>console.log(e));
	const [activeStory, setActiveStory] = useState("map");
		return (
			<Epic activeStory={activeStory} tabbar={
				<Tabbar>
					<TabbarItem
						onClick={()=>setActiveStory('map')}
						selected={activeStory === 'map'}
						data-story="map"
						text="Карта"
					><Icon28Search /></TabbarItem>
					<TabbarItem
						onClick={()=>setActiveStory('add')}
						selected={activeStory === 'add'}
						data-story="add"
						text="Добавить"
					><Icon28AddOutline /></TabbarItem>
				</Tabbar>
			}>
				<View id="map" activePanel="map">
					<Panel id="map">
						<PanelHeader
							left={<PanelHeaderButton onClick={()=>openSupport(!support)}>
								<Icon28ChevronDownOutline
								style={{ transform: `rotate(${support ? '180deg' : '0'})` }}
								onClick={()=>openSupport(!support)}
								/>
							</PanelHeaderButton>}
						>
							Карта
						</PanelHeader>
						<PanelHeaderContext style={{zIndex: 2}} opened={support} onClose={()=>openSupport(!support)}>
							<List>
								<Cell
									before={'*marker*'}
								>Вы здесь</Cell>
								<Cell
									before={'*marker*'}
									asideContent={filter.zero === true ? <Icon24Done fill="var(--accent)" /> : null}
									onClick={
										()=>{
											console.log(filter);
											filter.zero=!filter.zero;
											setFilter(filter);
										}
									}
								>Посты ДПС</Cell>
								<Cell
									before={'*marker*'}
									asideContent={filter.one === true ? <Icon24Done fill="var(--accent)" /> : null}
									onClick={
										()=>{
											let newFilter = filter;
											newFilter.one=!filter.one;
											setFilter(newFilter);
										}
									}
								>Стационарные камеры</Cell>
								<Cell
									before={'*marker*'}
									asideContent={filter.two === true ? <Icon24Done fill="var(--accent)" /> : null}
									onClick={
										()=>{
											let newFilter = filter;
											newFilter.two=!filter.two;
											setFilter(newFilter);
										}
									}
								>Передвижные камеры</Cell>
								<Cell
									before={'*marker*'}
									asideContent={filter.three === true ? <Icon24Done fill="var(--accent)" /> : null}
									onClick={
										()=>{
											let newFilter = filter;
											newFilter.three=!filter.three;
											setFilter(newFilter);
										}
									}
								>Дежурные экипажи</Cell>
							</List>
						</PanelHeaderContext>
						<MapComponent coords={coords}/>
					</Panel>
				</View>
				<View id="add" activePanel="add">
					<Panel id="add">
						<PanelHeader>Добавить</PanelHeader>
						<Add coords={coords}/>
					</Panel>
				</View>
			</Epic>
		);
};

export default App;

